import React from 'react';
import {
  Row, Col, Table
}
from 'antd';
import Panel from '../../components/panel/Panel';
import Card from '../../components/card/Card';
import Player from '../../components/player/Player';
import './OneDay.scss'

const data = [{
  key: '1',
  time: '09:00',
  len: '30',
  por: '30',
  dir: '',
  specie: '',
  speed: '-'
}, {
  key: '2',
  time: '10:00',
  len: '30',
  por: '30',
  dir: '',
  specie: '',
  speed: '-'
}, {
  key: '3',
  time: '11:00',
  len: '30',
  por: '30',
  dir: '',
  specie: '',
  speed: '-'
}, {
  key: '4',
  time: '12:00',
  len: '30',
  por: '30',
  dir: '',
  specie: '',
  speed: '-'
}, {
  key: '5',
  time: '13:00',
  len: '30',
  por: '30',
  dir: '',
  specie: '',
  speed: '-'
}];

export default class OneDay extends React.Component {
  state = {
    bordered: true,
    loading: false,
    pagination: false,
    size: 'small',
    scroll: undefined,
  }
  rowClick(record, index) {
    // debugger
  }
  render() {
    const columns = [{
      title: '',
      dataIndex: 'time',
    }, {
      title: '',
      dataIndex: 'len',
    }, {
      title: '',
      dataIndex: 'por',
    }, {
      title: '',
      dataIndex: 'dir',
    }, {
      title: '',
      dataIndex: 'specie',
    }, {
      title: '',
      dataIndex: 'speed',
    }];
    return <Panel>
              <Row>
                <Card>
                  <Col span={14}>
                    <img className="oneday-img" src="./images/fish.png" alt=""/>
                    <span className="oneday-text">:5.0</span>
                    <span className="oneday-text">:2m/s</span>
                  </Col>
                  <Col span={10}>
                    <Player url={'./images/463982.mp4'} loop={true}/>
                  </Col>
                </Card>
              </Row>
              <Row>
                <Card>
                  <Col span={16} offset={4}>
                    <div className="oneday-div"><span className="oneday-bntl"></span><span className="oneday-bntr"></span></div>
                    <Table
                      {...this.state}
                        columns={columns}
                        dataSource={data}
                        size="small"
                        onRowClick={this.rowClick.bind(this)}
                        bordered              
                    />
                  </Col>
                </Card>
              </Row>
            </Panel>
  }
}


